// Ember 3D Folding Panel Defaults
$ember-3d-folding-panel-defaults: (
  'accent-color': #544173, // Mulled Wine
  'color-6': #ffffff, // White
  'dark-color': #363558, // Martinique
  'fold-animation': .5s,
  'fold-color': #dadcdc,
  'fold-content-animation': .2s*(3+1)/2, // replace 3 with number of elements that you want to animate
  'itemSquareColors': #363558 #544173 #dadcdc #65d29b,
  'light-color': #ffffff, // White
  'overlay-color': #1c1726, // Bastille
);

// Empty config if user does not add their own
$ember-3d-folding-panel: () !default;

// Merge defaults and user added config
$ember-3d-folding-panel: map-merge($ember-3d-folding-panel-defaults, $ember-3d-folding-panel);
